html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

ul,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

@import '~office-ui-fabric-core/dist/sass/Fabric';
@import '~office-ui-fabric-js/dist/css/fabric.components';

.dialog {
  background-color: $ms-color-white;
  font-family: 'Segoe UI WestEuropean', 'Segoe UI', -apple-system, BlinkMacSystemFont, 'Roboto', 'Helvetica Neue', sans-serif;
  margin: 0 6px;
  overflow: hidden;

  .dialog-title {
    background-color: $ms-color-blue;
    color: $ms-color-white;
    font-size: 28px;
    font-weight: 100;
    padding-bottom: 6px;
    text-align: center;
  }

  .dialog-message {
    font-size: 18px;
    font-weight: 100;
    margin: 6px;
  }

  .input-validation-error {
    border: 2px solid $ms-color-red;
  }

  .field-validation-error {
    color: $ms-color-red;
  }
}
